
import { installRenderHelpers } from './render-helpers/index'
import VNode, { createEmptyVNode } from '../vdom/vnode'
import { createElement } from '../vdom/create-element'

/**
 * 
 * @param {Component} vm 
 */
export function initRender(vm) {
    vm._vnode = null; // the root of the child tree
    vm._staticTress = null; // v-once cached trees
    const options = vm.$options;
    //options._parentVnodeundefined

    //TODO: $slots


    /**
     * createElement
     * 
     * :  tag, data, children, normalizationType, alwaysNormalize
     * 
     */
    vm._c = (a, b, c, d) => createElement(vm, a, b, c, d, false);


    //(alwaysNormalize), render functions 
    vm.$createElement = (a, b, c, d) => createElement(vm, a, b, c, d, true);

    //TODO:vm $attrs, $listeners
}

export function renderMixin(Vue) {
    installRenderHelpers(Vue.prototype)

    Vue.prototype.$nextTick = function(fn) {}
    Vue.prototype._render = function() {
        const vm = this;
        //renderweb(web/entry-runtime-with-compiler.js)
        const {render} = vm.$options
        let vnode;
        try {
            console.log('render : ', render)
            //_renderProxyvm, render,vm.$createElement
            vnode = render.call(vm._renderProxy, vm.$createElement);
        } catch(e) {
            console.error(e);
        }

        if(!(vnode instanceof VNode)) {
            vnode = createEmptyVNode();
        }

        //set parent
        return vnode;
    }
}