import {
    remove,
    isObject,
    parsePath,
    // _Set as Set, Set
    handleError
} from '../util/index'

import {queueWatcher} from './scheduler'
import Dep, { pushTarget, popTarget } from './dep'

let uid = 0;

export default class Watch {
    constructor(vm, expOrFn, cb, options, isRenderWatcher) {
        this.vm = vm; 
        if(isRenderWatcher) {
            // watcher,watchervm_watcher
            vm._watcher = this;
        }
        //watcher
        vm._watchers.push(this);
        if(options) {
            this.computed = !!options.computed;
            this.user = !!options.user;
        } else {
            this.deep = this.user = this.computed = this.sync = false;
        }

        //TODO: 
        this.cb = cb;
        this.id = ++uid;
        this.active = true;
        //watcher,falsethis.value,this.get()
        this.dirty = this.computed; 
        this.deps = [];
        this.newDeps = [];
        this.depIds = new Set();
        this.newDepIds = new Set();
        this.expression = '';
   
        if(typeof expOrFn === 'function') {
            this.getter = expOrFn;
        } else {
            // watch
            this.getter = parsePath(expOrFn);
            
        }

        if(this.computed) {
            this.value = undefined;
            this.dep = new Dep();
        } else {
            /**
             * 1. value
             * 2. watchersubs
             */
            this.value = this.get();  
        }
    }

    /**
     * 1.computed
     * 2.data : , data
     */
    get() {
        pushTarget(this);
        let value;
        const vm = this.vm;
        try {
            value = this.getter.call(vm, vm);
        } catch(e) {
            console.error(e);
        } finally {
            popTarget();
            this.cleanupDeps();
        }
        return value;
    }

    /**
     * newDeps
     * watch
     * @param {Dep} dep 
     */
    addDep(dep) {
        const id = dep.id;
        if(!this.newDepIds.has(id)) {
            this.newDepIds.add(id);
            this.newDeps.push(dep);
            if(!this.depIds.has(id)) {
                dep.addSub(this);
            }
        }
    }

    cleanupDeps() {
        let i = this.deps.length;
        while(i--) { 
            const dep = this.deps[i];
            if(!this.newDepIds.has(dep.id)) {
                dep.removeSub(this);
            }
        }
        let tmp = this.depIds;
        this.depIds = this.newDepIds;
        this.newDepIds = tmp;
        this.newDepIds.clear();
        tmp = this.deps;
        this.deps = this.newDeps;
        this.newDeps = tmp;
        this.newDeps.length = 0;
    }

    update() {
        if(this.computed) {
            // : lazyactivated
            // lazy,,render
            if(this.dep.subs.length === 0) {
                // lazy, dirty
                //, this.evaluate()
                
                // ,this.dirtytrue,computed,
                this.dirty = true;
            } else {
                // activated, , 
                this.getAndInvoke(()=> {
                    // TODO: 
                    this.dep.notify();
                })
            }
        } else {
            console.log('');
            queueWatcher(this)
        }
    }

    // 
    run() {
        if(this.active) {
            console.log('%c ', 'color: green');
            this.getAndInvoke(this.cb);
        }
    }

    getAndInvoke(cb) {
        const value = this.get();
        console.log('running watcher value >>', this.computed, value, this.value);
        /**
         * 1. computed(b)(a), , value !== this.value
         */
        if(value !== this.value || isObject(value) || this.deep) { //this.deep
            //
            const oldValue = this.value;
            this.value = value;
            this.dirty = false;
            
            if(this.user) {
                try {
                    cb.call(this.vm, value, oldValue);
                } catch (e) {
                    //TODO:error
                }
            } else {
                cb.call(this.vm, value, oldValue);
            }
        }
    }

    /**
     * 
     */
    evaluate() {
        if(this.dirty) {
            this.value = this.get();
            this.dirty = false;
        }
        return this.value;
    }

    /**
     *  
     * 
     */
    depend() {
        if(this.dep && Dep.target) {
            this.dep.depend();
        }
    }

    /**
     * 
     */
    teardown() {
        if(this.active) {
            //TODO: destroyed

            let i = this.deps.length;
            while(i--) {
                this.deps[i].removeSub(this);
            }
            this.active = false;
        }
    }
}